'use strict';

const EventEmitter = require('events');
const AudioBuffer = require('./abuffer');

class Receiver extends EventEmitter {

  constructor() {
    super();
    this.audioCtx = null;
    this.streamUrl = null;
    this.procNode = null;
    this.worker = null;
    this.nsamples = 1024;
    this.aBuffer = null;
    this.suspended = false;
  }

  init(channel, audioCtx) {
    return new Promise((res, rej) => {
      if (typeof channel !== 'string') {
        return rej(new Error('Receiver: invalid channel'));
      }
      if (!audioCtx || isNaN(audioCtx.sampleRate)) {
        const AudioContext = window.AudioContext || window.webkitAudioContext;
        this.audioCtx = new AudioContext();
      } else {
        this.audioCtx = audioCtx;
      }
      this.channel = channel;
      this.aBuffer = new AudioBuffer(48000, this.nsamples * 4);
      res();
    });
  }

  audioProcess(e) {
    if (this.worker === null) return;
    const left = e.outputBuffer.getChannelData(0);
    const right = e.outputBuffer.getChannelData(1);
    const samples = this.aBuffer.read(this.nsamples * 2);
    let i = 0;
    let k = 0;
    if (samples) {
      for (i = 0; i < this.nsamples * 2; i += 2) {
        left[k] = samples[i];
        right[k] = samples[i + 1];
        ++k;
      }
    } else {
      for (i = 0; i < this.nsamples; ++i) {
        left[i] = 0;
        right[i] = 0;
      }
    }
  }

  start() {
    return new Promise((res, rej) => {
      if (this.suspended) {
        this.audioCtx.resume();
      } else {
        this.procNode = this.audioCtx.createScriptProcessor(this.nsamples,
         2, 2);
        this.procNode.onaudioprocess = (e) => {
          this.audioProcess(e);
        };
        this.procNode.connect(this.audioCtx.destination);
      }
      const Worker = require('worker?inline!./receiver.worker.js');
      const worker = new Worker();
      worker.onmessage = (e) => {
        if (e.data instanceof Float32Array) {
          return this.aBuffer.write(e.data);
        }
        if (e.data.type === 'init') {
          //console.log('ReceiverWorker initialized');
          this.worker = worker;
          res();
        } else if (e.data.type === 'eot') {
          this.emit('eot');
        } else if (e.data.type === 'disconnect') {
          this.emit('disconnect');
        } else if (e.data.type === 'error') {
          this.emit('error', e.data.err);
        }
      };
      worker.onerror = (e) => {
        this.emit('error', e);
      };
      this.url = 'https://alpha.facile.audio/rx';
      worker.postMessage({
        setup: {
          rate: this.audioCtx.sampleRate,
          nsamples: this.nsamples,
          channels: 2,
          url: this.url,
          channel: this.channel
        }
      });
    });
  }

  stop() {
    return new Promise((res, rej) => {
      this.audioCtx.suspend();
      this.suspended = true;
      if (this.worker) {
        this.worker.postMessage({destroy: true});
        this.worker = null;
      }
      res();
    });
  }

  reset() {
    this.stop();
    this.procNode.disconnect();
    this.audioCtx = null;
    this.streamUrl = null;
    this.channel = null;
    this.procNode = null;
    this.aBuffer = null;
  }

}

module.exports = Receiver;



