'use strict';

const EventEmitter = require('events');

function q2b(q) {
  if (typeof q !== 'string') return -1;
  switch (q) {
    case 'worst': return 32000;
    case 'low': return 64000;
    case 'medium': return 96000;
    case 'high': return 128000;
    case 'best': return 192000;
    default: return -1;
  }
}

class Transmitter extends EventEmitter {

  constructor() {
    super();
    this.audioCtx = null;
    this.mediaStream = null;
    this.streamUrl = null;
    this.srcNode = null;
    this.procNode = null;
    this.muteNode = null;
    this.worker = null;
    this.nsamples = 1024;
    this.frameSize = 960;
    this.bitRate = 128000;
    this.initialized = false;
    this.suspended = false;
  }

  init(setup) {
    if (!setup || !setup.audioCtx) {
      const AudioContext = window.AudioContext || window.webkitAudioContext;
      if (!AudioContext) {
        return Promise.reject(new Error('Missing AudioContext'));
      }
      this.audioCtx = new AudioContext();
    }
    if (setup && setup.mediaStream) {
      this.mediaStream = setup.mediaStream;
    }
    if (setup && setup.quality) {
      const ret = q2b(setup.quality);
      if (ret > 0) this.bitRate = ret;
    }
    return this._initAudio();
  }

  _capture() {
    if (this.mediaStream) return Promise.resolve(this.mediaStream);
    if (navigator.mediaDevices && navigator.mediaDevices.getUserMedia) {
      return navigator.mediaDevices.getUserMedia({
        audio: true,
        video: false
      });
    }
    navigator.getUserMedia = (navigator.getUserMedia ||
      navigator.webkitGetUserMedia ||
      navigator.mozGetUserMedia ||
      navigator.msGetUserMedia);
    return new Promise((res, rej) => {
      navigator.getUserMedia({
        audio: true,
        video: false
      }, (stream) => {
        res(stream);
      }, (e) => {
        rej(e);
      });
    });
  }

  _initAudio() {
    return this._capture().then((stream) => {
      this.srcNode = this.audioCtx.createMediaStreamSource(stream);
      if (this.srcNode.channelCount !== 2) {
        throw 'Source: input is not stereo!';
      }
      this.initialized = true;
    });
  }

  audioProcess(e) {
    const left = e.inputBuffer.getChannelData(0);
    const right = e.inputBuffer.getChannelData(1);
    const samples = new Float32Array(2 * this.nsamples);
    let i = 0;
    let j = 0;
    for (i = 0; i < this.nsamples; ++i) {
      samples[j++] = left[i];
      samples[j++] = right[i];
    }
    if (this.worker) {
      this.worker.postMessage(samples, [samples.buffer]);
    }
  }

  start() {
    if (this.initialized !== true) {
      return Promise.reject(new Error('Source has not been initialized!'));
    }
    return new Promise((res, rej) => {
      if (this.suspended === true) {
        this.audioCtx.resume();
      } else {
        this.procNode = this.audioCtx.createScriptProcessor(this.nsamples,
         2, 2);
        this.srcNode.connect(this.procNode);
        this.procNode.onaudioprocess = (e) => {
          this.audioProcess(e);
        };
        this.muteNode = this.audioCtx.createGain();
        this.muteNode.gain.value = 0.0;
        this.procNode.connect(this.muteNode);
        this.muteNode.connect(this.audioCtx.destination);
      }
      const Worker = require('worker?inline!./transmitter.worker.js');
      const worker = new Worker();
      worker.onmessage = (e) => {
        if (e.data.channel) {
          //console.log('Trasmitter Worker initialized');
          worker.postMessage({
            ctl: {
              req: 'OPUS_SET_BITRATE',
              val: this.bitRate
            }
          });
          this.worker = worker;
          res(e.data.channel);
        } else if (e.data.type === 'connect') {
          this.emit('connect');
        } else if (e.data.type === 'disconnect') {
          this.emit('disconnect', e.data.msg);
        } else if (e.data.type === 'error') {
          //console.log(e.data.err);
          this.emit('error', e.data.err);
        }
      };
      worker.onerror = (e) => {
        this.emit('error', e);
      };
      this.url = 'https://alpha.facile.audio/tx';
      worker.postMessage({
        setup: {
          rate: this.audioCtx.sampleRate,
          nsamples: this.nsamples,
          frameSize: this.frameSize,
          channels: 2,
          url: this.url
        }
      });
    });
  }

  stop() {
    if (this.suspended === false) {
      this.audioCtx.suspend();
      this.suspended = true;
    }
    if (this.worker) {
      this.worker.postMessage({reset: true});
      this.worker.terminate();
      this.worker = null;
    }
  }

}

module.exports = Transmitter;



